<template>
  <div class="markup-tables flex">
    <va-card color="divider" class="flex mb-4">
      <va-card-content>
        <div class="table-wrapper">
          <div class="">
            <legend/>{{ $t(`tables.headings.reviewed`) }}
            <va-chip class="mb-2 mr-2 add-button"
              small="true"
              color="#87CEEB"
              @click="addDialog='true'"
            >
              <span class="white-color">
                {{ $t('buttons.add')}}
              </span>
            </va-chip>
          </div>
          <table class="va-table">
            <thead>
              <tr>
                <th>{{ $t('tables.headings.account') }}</th>
                <th>{{ $t('tables.headings.name') }}</th>
                <th>{{ $t('tables.headings.department') }}</th>
                <th>{{ $t('tables.headings.operation') }}</th>
              </tr>
            </thead>
            <tbody>
              <!-- eslint-disable-next-line vue/no-unused-vars -->
              <tr v-for="(user, index) in users" :key="user.id">
                <td>
                  <auto-ellipsis :text="user.account" />
                </td>
                <td>
                  <auto-ellipsis :text="user.name" />
                </td>
                <td>
                  <auto-ellipsis :text="user.department" />
                </td>
                <td >
                  <va-icon-delete class="icon" :title="$t('titles.delete')" v-if="user != ''" @click="deleteDialog=true, selectedUser=user, selectedIndex=index" />
                </td>
              </tr>
            </tbody>
          </table>          
        </div>
      </va-card-content>
    </va-card>
  
    <va-card color="divider">
      <va-card-content>
        <div class="table-wrapper">
          <legend/>{{ $t(`tables.headings.beReviewed`) }}
          <table class="va-table">
            <thead>
              <tr>
                <th>{{ $t('tables.headings.account') }}</th>
                <th>{{ $t('tables.headings.name') }}</th>
                <th>{{ $t('tables.headings.department') }}</th>
                <th>{{ $t('tables.headings.reason') }}</th>
                <th>{{ $t('tables.headings.operation') }}</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="(user, index) in beReviewed" :key="user.id">
                <td>
                  <auto-ellipsis :text="user.account" />
                </td>
                <td>
                  <auto-ellipsis :text="user.name" />
                </td>
                <td>
                  <auto-ellipsis :text="user.department" />
                </td>
                <td>
                  <auto-ellipsis :text="user.reason" :notification="true" />
                </td>
                <td>
                  <va-icon-agree class="icon" :title="$t('titles.agree')" v-if="user != ''" @click="agree(user, index)" />
                  <va-icon-delete class="icon" :title="$t('titles.disagree')" v-if="user != ''" @click="disagree(user, index)" />
                </td>
              </tr>
            </tbody>
          </table>          
        </div>
      </va-card-content>
    </va-card>
  </div>

  <va-modal
    v-model="deleteDialog"
    position="top"
    size="large"
    :title=" $t('modal.tips')"
    :message=" $t('modal.deleteMessage')"
    :okText=" $t('modal.delete')"
    :cancelText=" $t('modal.cancel')"
    @ok="deleteRow"
  />

  <va-modal
    v-model="addDialog"
    position="top"
    size="large"
    :title=" $t('modal.addTips')"
    :message=" $t('modal.addMessage')"
    :okText=" $t('modal.add')"
    :cancelText=" $t('modal.cancel')"
    @ok="addUser"
  >
    <va-input
      v-model="account"
      @keypress.enter="addUser"
      :placeholder='$t("modal.account")'
      class="va-chat__input mr-2"
    />
    <br>
    <va-input
      v-model="name"
      @keypress.enter="addUser"
      :placeholder='$t("modal.name")'
      class="va-chat__input mr-2"
    />
    <br>
    <va-input
      v-model="department"
      @keypress.enter="addUser"
      :placeholder='$t("modal.department")'
      class="va-chat__input mr-2"
    />
  </va-modal>

</template>

<script>
import VaIconDelete from '@/components/icons/VaIconDelete.vue'
import VaIconAgree from '@/components/icons/VaIconAgree.vue'
import AutoEllipsis from '@/pages/admin/ui/txt/AutoEllipsis.vue'
import { reactive, onMounted } from 'vue'
import { get } from '@/axios/html'

export default {
  components: {
    VaIconDelete,
    VaIconAgree,
    AutoEllipsis
  },
  setup() {
    const state = reactive({
      users: [],
      beReviewed: []
    })
    onMounted(async() => {
      get("/api/user/getUsers", '')
        .then(res => {
          res.data.options.forEach(option => {
            state.users.push(option)
          });
        })
        .catch(err => {
          console.log(err)
        })
      get("/api/user/getBeRevieweds", '')
        .then(res => {
          res.data.options.forEach(option => {
            state.beReviewed.push(option)
          });
        })
        .catch(err => {
          console.log(err)
        })
    })
    return state
  },
  data () {
    return {
       addDialog: false,
       deleteDialog: false,
       account: "",
       name: "",
       department: "",
       selectedUser: '',
       selectedIndes: -1,
       toastColor: '#87CEEB',
       toastText: 'nullText',
       toastPosition: 'top-right',
       toastDuration: 2500,

    }
  },
  methods: {
    getStatusColor (status) {
      if (status === 'paid') {
        return 'success'
      }

      if (status === 'processing') {
        return 'info'
      }

      return 'danger'
    },
    launchToast() {
      this.$vaToast.init({
        message: this.toastText,
        position: this.toastPosition,
        color: this.toastColor,
        duration: Number(this.toastDuration)
      })
    },
    deleteRow() {
      get("/api/user/deleteUser", {
        account: this.selectedUser.account
      }).then(res => {
        if(res.data.code === '200') {
          this.users.splice(this.selectedIndex, 1)
          this.toastText = res.data.msg
          this.launchToast()
        }
      }).catch(err => {
        console.log(err)
      })
    },
    addUser() {
      get("/api/user/addUser", {
        account: this.account,
        name: this.name,
        department: this.department
      }).then(res => {
        if(res.data.code === '200') {
          this.users.push({
            account: this.account,
            name: this.name,
            department: this.department
          })
          this.toastText = res.data.msg
          this.launchToast()
          this.account = ''
          this.name = ''
          this.department = ''
        }
      }).catch(err => {
        console.log(err)
      })
    },
    agree(user, index) {
      get("/api/user/agree", {
        account: user.account
      }).then(res => {
        if(res.data.code === '200') {
          this.users.push(user)
          this.beReviewed.splice(index, 1)
          this.toastText = res.data.msg
          this.launchToast()
        }
      }).catch(err => {
        console.log(err)
      })
    },
    disagree(user, index) {
      get("/api/user/disagree", {
        account: user.account
      }).then(res => {
        if(res.data.code === '200') {
          this.beReviewed.splice(index, 1)
          this.toastText = res.data.msg
          this.launchToast()
        }
      }).catch(err => {
        console.log(err)
      })
    },
  },
}
</script>

<style lang="scss">


  .markup-tables {

    *::-webkit-scrollbar {
      position: fixed;
      top: 50px;
      /*滚动条整体样式*/
      width: 5px;
      height: 2px;
    }

    *::-webkit-scrollbar-thumb {
      /*滚动条里面小方块*/
      background-color: rgba(0, 0, 0, 0.2);
      border-radius: 5px;
    }

    *::-webkit-scrollbar-track {
      /*滚动条里面轨道*/
      background-color: unset;
      border-radius: 5px;
    }

    .table-wrapper {
      width: 100%;
      height: 38vh;
      margin: auto;
      overflow-y: auto;
      background: #e1e9f8;
    }

    .table-wrapper th {
      position: sticky;
      top: 0;
      left: 0;
      background: #e1e9f8;
    }

    .table-wrapper table {
      word-wrap: break-word; 
      word-break: break-all;
      text-align: center;
      table-layout: fixed;
      width: 100%;
      // height: 200px;
      margin: 0 auto;
      border-spacing: unset;
      border-top: unset;
    }

    .table-wrapper table#table > thead > tr:nth-of-type(odd) {
      background: rgba(204, 204, 204, 0.4);
    }

    .va-table {
      // overflow: auto;
      width: 100%;
      // background: #D3D3D3;
    }

    td {
      min-width: 500px;
    }

    tr {
      border-collapse: separate;
      border-radius: 50px;
    }

    tr:hover {
      background: #F0F8FF;
    }

    .add-button {
      float: right;
    }

    .white-color {
      color: white;
    }

    .icon {
      margin-right: 10px;
    }

  }
</style>
